import styled from 'styled-components';

const NavStyles = styled.ul`
  margin: 0;
  padding: 0;
  display: flex;
  justify-self: end;
  font-size: 1.5rem;
  .burger-container {
    position: fixed;
    top: 2%;
    right: 4%;
    z-index: 999;
    cursor: pointer;
    border: none;
    background: none;
    @media (min-width: 700px) {
      display: none;
    }
  }
  
.burger-container .burger-line-1, .burger-container .burger-line-2, .burger-container .burger-line-3 {
    border-radius: 2px;
    width: 35px;
    height: 5px;
    background-color: ${props => props.theme.semiblack};
    margin: 6px 0;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  
.change-burger .burger-line-1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }
  
.change-burger .burger-line-2 {
    opacity: 0;
  }
  
.change-burger .burger-line-3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }
  @media (max-width: 700px) {
    align-self: center;
    flex-direction: column;
    text-align: center;
    transition: .3s;
    // display: none;
  }
  a{
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    position: relative;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 1em;
    background: none;
    border: 0;
    cursor: pointer;
    color: ${props => props.theme.semiblack};
    @media (max-width: 700px) {
      padding: .5rem 10px;
      text-align: center;
      justify-content: center;
    }
  }
    &:hover,
    &:focus {
      outline: none;
    }
  }
  @media (max-width: 1300px) {
    font-size: 1.5rem;
  }
  .menu{
    // position: absolute;
    transition: .5s ease-in-out;
    @media(min-width: 700px){
      display: flex;
    }
  }
  .hide-menu{
    // display: none;
    transform: translateY(-250%);
    transition: .5s ease-in-out;
    }
  }
`;

export default NavStyles;
